/* **************** CARD SOCIALS ****************** */
.card-socials {
  background: transparent; }

.socials-item {
  background: #00acee;
  cursor: pointer;
  display: inline-block;
  float: left;
  height: 110px;
  margin-bottom: 30px;
  position: relative;
  transition: all .2s ease-in-out;
  width: 100px; }
  .socials-item:nth-last-child(1), .socials-item:nth-last-child(2) {
    margin-bottom: 0; }
  .socials-item:nth-child(2n) {
    float: right; }
  .socials-item.dr {
    background: #ed4a89; }
  .socials-item.gl {
    background: #d6492e; }
  .socials-item.fb {
    background: #3b589c; }
  .socials-item:hover {
    transform: scale(0.9);
    text-decoration: none; }
  .socials-item .zmdi {
    color: #fff;
    font-size: 63px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
